import TodoItemComponent from "../todoItem";
import "./style.css";

export default function TodoListComponent({ todoList, handleRemoveToDo }) {
  return (
    <ul className="todo-list-component-content">
      {todoList &&
        todoList.map((item, index) => {
          return (
            <TodoItemComponent
              todoItem={item}
              handleRemoveToDo={() => handleRemoveToDo(index)}
            />
          );
        })}
    </ul>
  );
}
